import CustomCheckBox from "components/CustomCheckBox";
import CustomForm from "components/CustomForm";
import CustomTable from "components/CustomTable";
import DocumentViewerModal from "components/DocumentViewer";
import useNotification from "hooks/Notifications";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getCsInUse, getPharmacyAdminData, setProfileData } from "redux/Actions/Auth";
import {
    deletePharmacyDocument,
    getCSInUse,
    getPharmacyCSInUse,
    postPharmacyInfo,
    uploadPharmacyDocument,
} from "redux/Actions/PharmacyAdminProfile";
import {
    ERROR_MESSAGES,
    FIVE_MB_SIZE,
    INPUT_TYPES,
    LABEL,
    PLACEHOLDERS,
    REGEX,
    SERVICES,
    SNACK_VARIANT,
    STRINGS,
} from "shared/Constants";
import { Images } from "shared/Images";
import { getDocsUrl, structureCSInUseData } from "shared/Utilities";
import { createUploadPayload, getDefaultValues, getInititalValueDocuments, structureAdminDocData } from "views/authenticated/constants";

